
.main {
  background-color: #ececec;
  color: #1d1c1a;
  font-family: 'Helvetica Neue', sans-serif;
  width: 100vw;
  font-weight: 900;
  font-size: 20px;
  position: relative;
  height: 100vh;
  display: block;
  overflow: hidden;
}
.main p {
  position: absolute;
  top:30%;
  left:30%;
}
.gif {
  background-image:url('/public/giphy.gif');
  background-repeat:repeat;
  width: 90%;
  height: 90%;
  margin: auto;
  position: relative;
  transition: all 1s;

}
.gif p {
  position: absolute;
  top:50%;
  left:50%;
  background-color: white;
  padding: 8px 20px 8px 20px;
  transform: translate(-50%,-50%);
  transition: all .3s ease-in-out;
}
.gif p:hover {
  background: black;
  color:white;
  cursor:pointer;
  transition: all .3s ease-in-out;
  padding: 5px 18px 5px 18px;
  
}


.oo {
  opacity: 0;
  transition: opacity 2s;

}
.p1 {
  opacity: 1;
  transition: opacity .3s;
}

@keyframes widthTrans {
  0% {
    width: 0;
    opacity:0;
  }
  50% {
    width: 0;
    opacity:0;
  }
  100% {
    width: 20rem;
    opacity:1;

  }
}
@keyframes textTrans {
  0% {
    opacity:0;
  }
  50% {
    opacity:0;
  }
  100% {
    opacity:1;

  }
}

.bwwrap {
  position: absolute;
  left: 50%;
  margin-top: 10rem;
  transform: translateX(-50%);
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 5rem ;
  width: 70rem;
  align-items: center;
  flex: 1 1;
}
.bw {
  position: relative;
  flex: 1 1;
  width: 20rem;
  height: 25rem;
  cursor: pointer;
  transition: all .5s ease-in-out;

}
.bw img {
  animation: 5s ease-in-out 0s 1 widthTrans;
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: all .5s ease-in-out;

}
.bw p {
  animation: 5s ease-in-out 0s 1 widthTrans;

  position: relative;
  display: block;
  top:unset;
  left:unset;
  font-weight: 800;
  font-size: 13px;
  text-align: left;
}
.bw img:hover {
  filter:  invert(100%) ;
  scale:0.9;
  transition: all .5s ease-in-out;
}
.flexmid {
  display: flex;
  align-items: center;
}